import {useState} from 'react'
import Cards from '../common/Cards'
import paginate from '../../utils/paginate'
import CategoryBar from '../common/CategoryBar'
import _ from "lodash";
import SortedBar from '../common/SortedBar'
import SearchBox from '../common/SearchBox'
import './Products.css'

function Products({ onSaleProducts,categories}) {
  

  const pageSize = 8
  const [currentPage, setCurrentPage] = useState(1)
  const [selectedCategory, setSelectedCategory] = useState({_id: 0 , name: '' })
  const [priceOrder, setPriceOrder] = useState('')
  const [searchQuery, setSearchQuery] = useState('')

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const handleCategorySelect = (category) => {
    setSelectedCategory(category);
    // searchQuery: "", // genre
    setCurrentPage(1);  // 12moviespageSize=4startIndex=4
    // ->paginate(filtered)filteredmovies3statecurrentPage2index=4
  };

  const handlePriceOrder = (order) => {
    setPriceOrder(order);
    setCurrentPage(1)
  }

  const handleSearchQuery = (query) => {
    setSearchQuery(query);
    setCurrentPage(1)

  }
  
  // 
  const itemsCount = onSaleProducts.length

  let categorySortedData = onSaleProducts;
  if (categories && selectedCategory.name !== '') {
     categorySortedData = onSaleProducts.filter( data => data.category.name === selectedCategory.name)  
  } 
  

  let priceSortedData = categorySortedData
  // priceOrder
  if (priceOrder) {
    priceSortedData = _.orderBy(
      categorySortedData,
      ['price'],
      priceOrder
    )
  }
  
  const searchedData = priceSortedData.filter(data => data.title.toLowerCase().includes(searchQuery.toLowerCase()))
  
  const paginatedData = paginate(searchedData, currentPage, pageSize)

  return (
    <>
      <h1 className='products-title'></h1>
      <div className='products-operation'>
      <CategoryBar
        items={categories}
        onItemSelect={handleCategorySelect}
        selectedItem={selectedCategory}
      />
      <SortedBar
        orderState={priceOrder}
        onPriceOrder={handlePriceOrder}
      />
      <SearchBox
        value={searchQuery}
        placeholder='...'
        onChange={handleSearchQuery} />
      </div>
      {/* data... */}
      {paginatedData.length === 0
        ? <h3 className='not-searched'></h3>
        : <Cards
            data={paginatedData}
            pageSize={pageSize}
            itemsCount={itemsCount}
            currentPage={currentPage}
            onPageChange={handlePageChange}
            onCategoryChange={handleCategorySelect}
          />
      }
    </>
  )

  
}

export default Products