import React, { useState,  useContext} from 'react'
import { Link } from 'react-router-dom'
import { CartContext } from '../App'
import './Navbar.css'

function Navbar({ user }) {

    const [clicked, setClicked] = useState(false)
  
    const {cart} = useContext(CartContext)

    // false settruetrue setfalse
    const handleClick = () => setClicked(!clicked)
    const closeMobileMenu = () => setClicked(false)

    
  return (
      <>
          <nav className='navbar'>
              <Link to='/' className='navbar-logo' onClick={closeMobileMenu}>
                TRVL <i className='fab fa-typo3' />
              </Link>
              <div className="menu-icon" onClick={handleClick}>
                  {/* setClick */}
                  <i className={clicked ? 'fas fa-times' : 'fas fa-bars'}/>
              </div>
                {/* setClickcss */}
              <ul className={clicked ? 'nav-menu active' : 'nav-menu'}>
                  <li className="nav-item">
                      <Link to='/' className='nav-links' onClick={closeMobileMenu}></Link>
                  </li>
                  <li className="nav-item">
                      <Link to='/products' className='nav-links' onClick={closeMobileMenu}></Link>
                  </li>
                <li className="nav-item">
                      <Link to='/cart' className='nav-links' onClick={closeMobileMenu}></Link>
                      <span className="position-relative translate-middle badge rounded-pill bg-danger">{cart.length  }</span>
                  </li>
                  {user && (
                      <>
                  <li className="nav-item">
                      <Link to='/update-products' className='nav-links' onClick={closeMobileMenu}></Link>
                  </li>
                    <li className="nav-item">
                    <Link to='/profile' className='nav-links' onClick={closeMobileMenu}>: {user.name}</Link>
                      </li>
                      <li className="nav-item">
                    <Link to='/logout' className='nav-links ' onClick={closeMobileMenu}></Link>
                  </li>
                     </> 
                  )}
                  {!user && (
                      <>
                    <li className="nav-item">
                    <Link to='/login' className='nav-links' onClick={closeMobileMenu}></Link>
                      </li>
                      <li className="nav-item">
                    <Link to='/register' className='nav-links' onClick={closeMobileMenu}></Link>
                  </li>
                     </> 
                  )}
              </ul>
          </nav>
      </>
  )
}

export default Navbar