import http from "./httpService";

const apiEndpoint = "products";

function productUrl(id) {
  return `${apiEndpoint}/${id}`;
}

export async function getProducts() {
  return await http.get(apiEndpoint).catch(e => { console.log('e',e)});
}

export async function getProduct(product_id) {
  return await http.get(productUrl(product_id)).catch(e => { console.log('e',e)});
}

export async function deleteProduct(product_id) {
  return await http.delete(productUrl(product_id)).catch(e => { console.log('e', e)});
}

export async function saveProduct(product) {
  // create new product
  if (!product._id) {
    // productInDb._id = Date.now().toString(); // productIdtoString()product
    return await http.post(apiEndpoint, product).catch(e => { console.log(e)});
  }
  
  // update old product
  const body = { ...product }; // 
  delete body._id;
  return http.put(productUrl(product._id), body); // _idrequest bodyproduct._idproduct._idapi url_id?_idbody

  //   return productInDb;
}
