import axios from "axios";
import logger from "./logService";
import { toast } from "react-toastify"; // toastfuncjavascriptfunc
// toasterrorsuccessinfotoast()

// baseURLaxiosbaseURL
axios.defaults.baseURL = process.env.REACT_APP_API_URL; // devproduction envenv
// dev envbaseURL=http://localhost:3900/api
// production envbaseURLherokuvidly-api-nodehttps://ntense-reef-87676.herokuapp.com/api
// use()func12error
// interceptorsPromise.reject(error)func
axios.defaults.headers.common["Access-Control-Allow-Origin"] = "*";

axios.interceptors.response.use(null, (error) => {
  console.log("");
  const expectedError =
    error.response &&
    error.response.status >= 400 &&
    error.response.status < 500; // errorresponsestatus400~500errorexpected

  // 
  if (!expectedError) {
    console.log("", error);
    logger.log(error); // sentry
    toast.error(""); // 
  }

  return Promise.reject(error); //  returnpassfunccatch
});

// jwthttp requestheadersjwtpost,put,delete
// axioshttpheaders['x-auth-token']
// commonheadersget, post, put, deleteCRUDjwt
