// !!!bi-directional dependency

// authService()()
export function setJWT(JWT) {
  axios.defaults.headers.common["x-auth-token"] = JWT;
}

// {}axios
// axiosaxios
// httpaxios
export default {
  get: axios.get,
  post: axios.post,
  put: axios.put,
  delete: axios.delete,
  setJWT,
};
