import {useState} from "react";
import Joi from "joi-browser";
import Form2 from "../common/Form2";
import auth from "../service/authService";
import Home from './Home'
import { useToasts } from "react-toast-notifications";
import { useNavigate } from 'react-router-dom';


function Register() {
  
  const [errors, setErrors] = useState({})
  const { addToast } = useToasts()
  const navigate = useNavigate()

  const schema = {
    email: Joi.string().email().trim().required(),
    password: Joi.string()
      .regex(/[a-zA-Z0-9]{5,30}$/)
      .required(),
    password2: Joi.string()
      .regex(/[a-zA-Z0-9]{5,30}$/)
      .required(),
    name: Joi.string().min(3).trim().required(),
  };

  const checkPassword = (data) => {
    console.log('do checkPassword ')
    if (data.password2 === '') return '';
    return (data.password === data.password2 ) ? '' :  '';
  }

  const doSubmit = async (data) => {
   
    try {
      await auth.register(data);
      navigate('/login', {state:'register'})
      
    } catch (ex) {
      if (ex.response && ex.response.status === 400) {
        errors.message  = ex.response.data;
        setErrors(errors.message);
        addToast(`${errors.message}`,{appearance: 'error'})
      }
    }
  };

  if (auth.getAuthUser()) return <Home /> 
      
  // login
      
  
  return (
      <>
        <h1></h1>
        <Form2
          cpnts={[
            { cpnt: 'Input', label:':',name: 'email' ,type:'email', placeholder:'abc@gmail.com'},
            { cpnt: 'Input', label:':',name: 'password', type:'password' ,placeholder:'5' },
            { cpnt: 'Input', label:':',name: 'password2', type:'password' ,placeholder:'' },
            { cpnt: 'Input', label:':',name: 'name', type:'text' ,placeholder:'3' },
          { cpnt: 'Button', label: '', name: 'submit', type: 'submit' },
            { cpnt: 'Link', label:'',name: '',path:'/login' }

        ]}
        schema={schema}
        checkPassword={checkPassword}
        onSubmit={doSubmit}
        errors={errors}
        />
     
      </>
    );
}
  



export default Register;


