import http from "./httpService";
import jwtDecode from "jwt-decode";

const registerApiEndpoint = "users"; // register

const loginApiEndpoint = "auth"; // login

const token = "token";

http.setJWT(getJWT());

export function getJWT() {
  return localStorage.getItem(token);
}

export async function login(user) {
  // loginres.dataJSON web token
  const { data: JWT } = await http.post(loginApiEndpoint, user);
  localStorage.setItem(token, JWT); // 1localStorage keystring2valuestring
}

export async function register(user) {
  // registerres.headersx-auth-token
  const res = await http.post(registerApiEndpoint, user);
  localStorage.setItem(token, res.headers["x-auth-token"]);
}

export  function getAuthUser() {
  try {
    const jwt =  localStorage.getItem(token);
    return jwtDecode(jwt); // user
  } catch (ex) {
    return null;
  }
}

export  function logout() {
  localStorage.removeItem(token);
}

const auth = {
  login,
  register,
  getAuthUser,
  logout,
  getJWT,
};

export default auth;
