import React,{useState,useEffect} from 'react'
import Input from "./Input";
import { Link } from 'react-router-dom';
import Joi from "joi-browser";
import './Form2.css'

function Form2({ cpnts, onSubmit, schema, checkPassword, errors}) {
    
    const [data2, setData2] = useState({})
    const [errors2, setErrors2] = useState(null)
    // Form2errors2loginregistererrors
    useEffect(() => {
        setErrors2(errors)
    },[])
    
    console.log('Form2 data', data2)
        
    const validate = (schema) => {
        const options = { abortEarly: false }; // 3falseerror
        const { error } = Joi.validate(data2, schema, options);
        if (!error) return null; // errorsnull

        const errors = {};
        for (let item of error.details) {
        errors[item.path] = item.message;
        }
        return errors;
    };

    const handleSetData2 = (key) => (event) => {  
        setData2(state => {
            state[key] = event.target.value
            return state;
        })
        console.log('data2',data2)
    }

   const handleSubmit = (schema , data, checkPassword=null) =>(event)=> {
    event.preventDefault();
       let errors = validate(schema);
    //  
       if (checkPassword && checkPassword(data)) {
         // errorsnullerrors{}errorsnullkey,value
           if (!errors) errors = {};
           errors['password2'] = checkPassword(data);
       }
       //data.password2
       setErrors2(errors)
 // usernamepassworderrorerrors<
    // ->  || orerrorsnull{}
    if (errors) return; // errors
        delete data.password2 
       onSubmit(data2);
   };
    
   
    
    return (
        <form onSubmit={handleSubmit(schema, data2, checkPassword)} className='form2'>
        {cpnts.map(item => {
            let { name, type,label,placeholder,path } = item
                switch (item.cpnt) {
                    case 'Input':
                        return (
                            <Input
                                key={name}
                                name={name}
                                label={label}
                                type={type}
                                placeholder={placeholder}
                                errors2={errors2}
                                onSetData2={handleSetData2}
                            // data
                            // onChange={handleChange}
                            // error={errors[name]}
                            />)
                    case 'Button':
                        return (
                            <input
                                key={name}
                                type={type}
                                value={label}
                                className="btn btn-primary mt-4"
                                // disabled={errors2} // errortruthydisabledsubmit
                            />)
                    case 'Link':
                        return (
                            <p key={name} className='form2-p'>
                            <span>{label}</span>
                            <span><Link to={`${path}`}>{name}</Link></span>
                            </p>
                        )
                    default:
                        return <h3></h3>

                }
            })
        }
    </form>
    )
}
        
    
    
export default Form2;