import {useState, useEffect} from "react";
import Joi from "joi-browser";
import Form2 from "../common/Form2";
import auth from "../service/authService";
import Home from './Home'
import { useNavigate,useLocation } from "react-router-dom";
import { useToasts } from "react-toast-notifications";


function Login({user,setUser}) {

  const navigate = useNavigate()
  const location = useLocation()
  const { addToast } = useToasts()
  
  const [errors, setErrors] = useState({})


  useEffect(() => {
    if (location.state === 'cart') {
      addToast('',
      {appearance: 'info'}
      )
    }
    if (location.state === 'register') {
      addToast('',
      {appearance: 'success'}
      )
    }
  },[location.state])

    if (user) return <Home />

  const schema = {
        email: Joi.string().required().label("email"), // labelerror
        password: Joi.string().min(5).required().label("password"),
  };
  

  const doSubmit = async (data) => {
    // const { state } = this.props.location;
    // console.log("location state", state);
    try {
      await auth.login(data);
      let user = auth.getAuthUser()
      setUser(user)
      navigate('/', {state:'login'})
    //   this.props.history.push("/"); // App.jsCMD
      // Routepropslocation
      // login"/"
    //   window.location = state ? state.from : "/";
    } catch (ex) {
      if (ex.response && ex.response.status === 400) {
        errors.message = ex.response.data;
        setErrors(errors);
        addToast(`${errors.message}`,{appearance: 'error'}
        )
      }
    }
  };

      
  // login
      
  
  return (
      <>
        <h1></h1>
        <Form2
          cpnts={[
            { cpnt: 'Input', label:':',name: 'email' ,type:'email', placeholder:'abc@gmail.com'},
            { cpnt: 'Input', label:':',name: 'password', type:'password' ,placeholder:'5' },
          { cpnt: 'Button', label: '', name: 'submit', type: 'submit' },
            { cpnt: 'Link', label:'',name: '', path:'/register' }


        ]}
        schema={schema}
        onSubmit={doSubmit}
        errors={errors}
        />
        
      </>
    );
}
  



export default Login;

