import _ from "lodash";

// 12click3item
// paginatereuse1moviesitems
export default function paginate(items, pageNumber, pageSize) {
  const startIndex = (pageNumber - 1) * pageSize;
  // indexmovieindex
  // index =  -1 items
  // pageNumber = 2(), pageSize= 445movie
  // let a = _(items); // []lodash
  // let b = _(items).slice(startIndex); // startIndexindex
  // let c = _(items).slice(startIndex).take(pageSize); // itemsstartIndexitems
  let d = _(items).slice(startIndex).take(pageSize).value(); // lodasharray
  return d;
}
