import React from "react";

// ...rest : value, type, onChangeddestruction<Input value={value} ...>props
const Input = (props, { ...rest }) => {

 
  return (
    <div className="form-group mt-4">
      <span className="required">*</span>
      <label htmlFor={props.name}>{props.label}</label>
      <input
        // value={this.state.data.xxx}inputLoginForm state
        //   ref={this.ref}
        {...rest}
        className="form-control"
        type={props.type}
        name={props.name}
        placeholder={props.placeholder}
        onChange={props.onSetData2(props.name)}
      />

      {/* props.errorundefined[props.name] */}
      {/* props.error[props.name]input */}
      {props.errors2 && props.errors2[props.name] && <div className="alert alert-danger">{props.errors2[props.name]}</div>}
    </div>
  )
};


export default Input;