import React, { useState } from "react";
import Table from "./Table";
import paginate from "../../utils/paginate";
import CategoryBar from "./CategoryBar";
import SearchBox from "./SearchBox";
import _ from 'lodash'
import Pagination from "./Pagination"; 

// step1:func componentclass component
// step2:MoviehandleSort
function ProductTable({ user, dbProducts, categories,columns  }) {
  // admin

  const [pageSize, setPageSize] = useState(8)
  const [currentPage, setCurrentPage] = useState(1)
  const [selectedCategory, setSelectedCategory] = useState({ _id: 0 , name: ''})
  const [searchQuery, setSearchQuery] = useState('')
  const [sortedColumn, setSortedColumn] = useState( {path: "title", order: "asc" })


    // if (user && user.isAdmin) this.columns.push(this.deleteColumn());

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const handleCategorySelect = (category) => {
    setSelectedCategory(category);
    // searchQuery: "", // genre
    setCurrentPage(1);  // 12moviespageSize=4startIndex=4
    // ->paginate(filtered)filteredmovies3statecurrentPage2index=4
  };

  const handleSearchQuery = (query) => {
    setSearchQuery(query);
    setCurrentPage(1)
  }

   const handleSort = (sortedColumn) => {
    setSortedColumn(sortedColumn); // ascending
  };
  

  const itemsCount = dbProducts.length

  
  let categorySortedData = dbProducts;

  if (categories && selectedCategory.name !== '') {
     categorySortedData = dbProducts.filter( data => data.category.name === selectedCategory.name)  
  } 
  

  let sortedData= categorySortedData
  
  // priceOrder
  sortedData = _.orderBy(
    categorySortedData,
    [sortedColumn.path],
    [sortedColumn.order]
  )
  
  const searchedData = sortedData.filter(data => data.title.toLowerCase().includes(searchQuery.toLowerCase()))
  
  const paginatedData = paginate(searchedData, currentPage, pageSize)



    return (
      <div className="table-container">
      <CategoryBar
        items={categories}
        onItemSelect={handleCategorySelect}
        selectedItem={selectedCategory}
      />
      <SearchBox
        value={searchQuery}
        placeholder='...'
        onChange={handleSearchQuery} />
    
        <Table
          items={paginatedData}
          columns={columns}
          sortedColumn={sortedColumn}
          onSort={handleSort}       
        />
        <Pagination
        // Paginationeventhandle event
        onPageChange={handlePageChange}
        itemsCount={itemsCount} // 
        pageSize={pageSize}
        currentPage={currentPage} // 
        />
      </div>
    );
}


export default ProductTable;
